body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Home */
.home {
  height: 100%;
}

/* Header */
.header-home {
  background-color: #51a3a3;
  color: azure;
  height: auto;
  width: 100%;
  font-family: "Sniglet", cursive;
  text-align: center;
  font-size: 3.5em;
  word-wrap: break-word;
  padding: 5% 0%;
}
/* Common */
.title {
  font-family: "Sniglet", cursive;
  text-align: center;
  font-size: 2.5em;
  padding: 2% 0%;
}

.text {
  font-family: "Sniglet", cursive;
  font-size: 1.5em;
  padding: 0% 0 2% 0%;
  overflow-y: auto;
}
/* About me */
.about-me {
  background-color: #4c5b5c;
  color: azure;
}
/* Skills */
.skills {
  background-color: #ff715b;
  color: azure;
}
.skills-bullet {
  text-align: left;
  list-style: none;
}
.list {
  display: inline-block;
  margin: 0 5.5%;
  -webkit-padding-start: 0%;
          padding-inline-start: 0%;
}

/* Projects */
.projects {
  background-color: #e6be93;
  color: azure;
}
.project-title {
  font-size: 2em;
}
.project-desc {
  margin: 1% 10%;
  font-size: 1.25em;
  text-align: center;
}
.image {
  width: 60%;
  overflow: auto;
  margin: auto;
}
.project-preview {
  width: 100%;
  max-height: 500px;
}
.indv-project {
  margin: 2.5% 0 0 0;
}

/* Contact Me */
.contact-me {
  background-color: #4c5b5c;
  color: azure;
}
/* Smaller Screens */

@media only screen and (max-width: 600px) {
  /* Header */
  .header-home {
    background-color: #51a3a3;
    color: azure;
    height: auto;
    width: 100%;
    font-family: "Sniglet", cursive;
    text-align: center;
    font-size: 2em;
    word-wrap: break-word;
    padding: 5% 0%;
  }
  /* Common */
  .text {
    font-family: "Sniglet", cursive;
    font-size: 1em;
    padding: 0% 0 2% 0%;
    overflow-y: auto;
  }
  .title {
    font-family: "Sniglet", cursive;
    text-align: center;
    font-size: 1.5em;
    padding: 2% 0%;
  }
  /* Skills */
  .skills-bullet {
    text-align: center;
    list-style: none;
    font-size: 0.75em;
  }
  .list {
    display: inline-block;
    margin: 0 3.5%;
    -webkit-padding-start: 0%;
            padding-inline-start: 0%;
  }
  /* Projects */
  .project-preview {
    width: 100%;
    height: 100px;
  }
  .project-desc {
    margin: 1% 10%;
    font-size: 1em;
    text-align: center;
  }
}
